.skiplink {
	position: absolute;
	z-index: 99999;
	background-color: #333;
	color: #fff;
	padding: 0.3em 0.5em;
}

body {
	:not(.cta):focus-visible {
		outline: none;
		box-shadow: 0 0 0 3px rgba($ui-foreground-primary-accent-01, 0.5);
		border-radius: 0;
		transition: all 0.4s ease;
	}
}
